.header-wrap {
	background: url(https://source.unsplash.com/nqSUnXINsp4/1600x900) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 47em;
}

.ui.container.main-content {
	margin: 7em 0;
}

.ui.small.icon.buttons {
	margin-bottom: 10px;
}

.ui.borderless.main.menu {
	border: none;
    margin: 0;
}


.logotext {
	font-family: 'bagnard';
	margin-top: .2em !important;
	/*font-family: 'young-serif';*/
}

.page-heading {
	font-size: 5em;
}

.tagline {
	/*font-family: 'et-book-italic';*/
	/*font-family: 'oldenburg';
	font-size: 1.5em;*/
	font-family: cmutypewritertextbold;
    font-size: 1.5em;
    font-kerning: normal;
    text-transform: uppercase;
}

/*.heading {
	font-family: 'CMUTypewriterTextItalic';
	text-transform: uppercase;
	font-family: Inknut Antiqua;
	font-family: 'bagnard';
	font-family: 'et-book-italic';
}*/

.workshop-list {
	padding: 0;
}

.workshop-heading {
	margin-top: 0;
}

.workshop-date {
	font-family: 'Oldenburg';
	float: right;
	margin-top: .6em;
}

.workshop-description {
	clear: both;
}

.tertiary-font {
	font-family: 'oldenburg';
}

.workshop-post-content {
	width: 25em;
}

.aside-tagline {
	font-family: cmutypewritertextbold;
    font-size: 1.7em;
    letter-spacing: 0.09em;
    margin-bottom: .5em;
    text-transform: uppercase;
}

ul {
	list-style-type: circle;
}

.workshop-featured-image {
	margin-bottom: 3em;
}

.pay-btn {
	margin-top: 1em !important;
}


.ui.sticky {
	position: relative;
	left: 1.5px !important;
    top: -11px;
    transition: top 0.5s ease-in;
}

.ui.sticky.fixed.top {
	top:80px !important;
	left: 211px !important;
}

.collection-heading {
	margin-bottom: 1.5em;
}

nav ul {
	list-style-type: none;
}

nav ul li {
	display: inline;
	margin-right: 1em;
}

.stick {
	position: sticky;
	top: 180px;
}

.footer {
	background-color: 'lavenderblush';
}



